import React from 'react'
import PropTypes from 'prop-types'
import Context from '../pages/content'
import Demo02 from './Demo02'


const Demo01 = (props) => {
    return (
        <div style={{width:100,height:'100px',background:'red'}}>
            {/* {props.name} */}
            <h2>Context</h2>
            <ul>
            {/* <Context.Consumer>
                {
                    // (data)=>{
                    //     return <li>
                    //         {data.data}
                    //     </li>
                    // }
                    //数组
                    (data)=>data.data.map((item)=><li key={item}>{item}</li>)
                }
            </Context.Consumer> */}
            <Context.Consumer>
                    {
                        ({data})=>data.map(
                            item=><li key={item}>{item}</li>
                        )
                    }
                </Context.Consumer>
            </ul>
            <Demo02/>
        </div>
    )
}
Demo01.propTypes = {
    name:PropTypes.string    //判断属性类型是否正确（和index中传入值）
}
Demo01.defaultProps = {
    name:'tom'
}
export default Demo01;
