
//快捷键
//imp-   import moduleName from 'module'
//imr→	import React from 'react'
//rcc-  类组件
//rfc-  函数组件

import React, { Component } from 'react'
import './control.css';




export default class Contral extends Component {
    constructor(){
        super();
        this.state = {
            inpValue:'',
            todos:[
                // {title:'1',done:false},
                // {title:'2',done:true},
                // {title:'3',done:true},
                // {title:'4',done:false},
            ]
        }
    }
    handleonChange=(e)=>{
       // console.log(e.target.value);
       this.setState({inpValue:e.target.value})     
    }

    componentDidMount(){
        this.input.focus();
        let todos = localStorage.getItem('todos');
        if(todos){
            this.setState({
                todos: JSON.parse(todos)
            })
        }
        
    }
    componentDidUpdate(){
        localStorage.setItem(
            'todos',
            JSON.stringify(this.state.todos)
        )
    }
    addTodo=()=>{
          // this.state.todos.push({title:this.state.inpValue})
        // this.setState({
        //     todos:this.state.todos
        // })
        if(this.state.inpValue !==''){
            this.setState({
                todos:[
                    {
                        title:this.state.inpValue,
                        done:false
                    },
                    ...this.state.todos
                ]
            })
            this.setState({inpValue:''})
        }
        
    }
    delTodo=(idx)=>{
        // this.state.todos.splice(idx,1);
        // this.setState({todos:this.state.todos})

        //深拷贝出来一份
        // let newTodos = JSON.parse(JSON.stringify(this.state.todos));
        // newTodos.splice(idx,1);
        // this.setState({todos:new  Todos});

        this.setState({
            todos: this.state.todos.filter((item,index)=>index!=idx)
        });

        // setState 是异步执行的，注意callback的用法
        // this.setState({
        //     todos: this.state.todos.filter((item,index)=>index!=idx)
        // },()=>{
        //     localStorage.setItem(
        //         'todos',
        //         JSON.stringify(this.state.todos)
        //     )
        // });

    }
    toggle = (index)=>{
        let newTodos = JSON.parse(JSON.stringify(this.state.todos));
        newTodos[index].done = !newTodos[index].done;
        this.setState({
            todos:newTodos
        })
    }
    renderTodos = (done)=>{
        return this.state.todos.map((todo,idx)=>{
            if(done==todo.done){
               return <li>
                   <input 
                        onClick = {()=>this.toggle(idx)}
                        checked={done}
                        type="checkbox"
                   />
                   <span dangerouslySetInnerHTML={{__html:todo.title}}></span>
                   {/* 可以读取<h1></h1>等标签内容 */}
                   <button onClick={()=>this.delTodo(idx)}>删除</button>
               </li>
            }
           })
    }
    render() {
        // localStorage.setItem('todos',JSON.stringify(this.state.todos))
        const{todos,inpValue} = this.state;
        let arr1 = todos.filter(item =>!item.done);
        
        //{
            // console.log(item,index);        
        // }
        // console.log(arr1);

       
        return (
            <div className="box">
                <label hemlFor="inp">todolist</label>
                {/* 光标 */}
                 {/* { {<input value="hello" type="text"/>  加上value，不能再输入 }} */}
               
               
               <input
               ref={inp=>this.input = inp} //获取结点
               value={inpValue} 
               type="text"
               onChange= {this.handleonChange}
               /> 
               
               
               <button onClick={this.addTodo}>添加</button>
               <h2>正在进行{arr1.length}</h2>
               <ul>
                   {this.renderTodos(false)}
               </ul>
               <h2>已经完成</h2>
               <ul style={{color:3<2?'red':'green'}}>
                   {this.renderTodos(true)}
               </ul>
               {
                   3>2?<button>登录</button>
                   :<span>hello</span>
               }
               <button>{2>3?'登录':'注册'}</button>
            </div>
        );
    }
}
