import React from 'react'
import {NavLink, Route, Redirect,withRouter} from 'react-router-dom'
import {BrowserRouter as Router,Switch} from 'react-router-dom'
import './doc.css'
import RouteWithSubRoutes from '../RouteWithSubRoutes'


export function Core(){
    return<h1>
        core
    </h1>
}
export function Guide(){
    return<h1>
        guide
    </h1>
}
export function Api(){
    return<h1>
        api
    </h1>
};
export function Hooks(){
    return<h1>
        hooks
    </h1>
}


//Navbar
const Doc = ({routes}) => {
    //console.log(props);
    // console.log({routes});    
    return <Router>
        <div className='doc'>
            <div className="content">
                {/* 嵌套路由 */ }
                {/* <Route path='/doc/:type' component={ DocContent } /> */}    
                <Redirect from='/doc' to='/doc/core' />          
                <Switch>
                    {routes.map((route, i) => (
                        <RouteWithSubRoutes key={i} {...route}/>
                    ))}
                </Switch>            
            </div>
                <ol>
                    <li><NavLink to='/doc/core'>核心概念</NavLink></li>
                    <li><NavLink to='/doc/guide'>高级指引</NavLink></li>
                    <li><NavLink to='/doc/api'>API</NavLink></li>
                    <li><NavLink to='/doc/hooks'>Hooks</NavLink></li>
                </ol>  
        </div>
    </Router>
}

export default withRouter(Doc); 
