import React from 'react'
import {BrowserRouter as Router,Redirect,Route} from 'react-router-dom'
const PrivateRoute = ({component:Com,routes, ...rest}) => {
   	return (
		<Route
			{...rest}
			render={({location}) =>
				localStorage.getItem('userinfo')
					?<Com routes={routes}/>
					:(
						<Redirect
							to={{
								pathname:"/login",
								state:location
							}}
						/>
					)
			}
		/>
	)
}

export default PrivateRoute;


