//数组方式路由
const ReactIndex = ()=>{
	return <Router>
		<Header />
		<Switch>
			{
				// routes.map(route => {
				// 	if(route.pri){
				// 		return <PrivateRoute {...route}/>
				// 	}
				// 	return <Route {...route}/>  
				// })
				routes.map(({pri,...rest}) => {
					if(pri){
						return <PrivateRoute {...rest}/>
					}
					return <Route {...rest}/>  
				})
			}
		</Switch>
	</Router>
}
export default ReactIndex;


