import React from 'react';
//第二种 import React,{Component} from 'react';
import List from '../components/List';
import Buttons from '../components/Buttons';



class Home extends React.Component{
// 第二种，接上 class Home extends Component{
    constructor(props){
      super(props);
      this.state = {
        data:[]
      }
      // this.handleClick = this.handleClick.bind(this)
    }
    componentDidMount(){
      fetch('https://cnodejs.org/api/v1/topics')
      .then(res=>res.json())
      .then( ({data}) => {
        console.log(data);
        this.setState({data})
      })
    }
    getData = (page)=>{
        fetch('https://cnodejs.org/api/v1/topics?page='+page)
        .then(res=>res.json())
        .then( ({data}) => {
          console.log(data);
          this.setState({data})
        })
      console.log(page);
    }
    render(){
      const { data } = this.state;
    //   const btns = [1,2,3,4,5];
      return <div>
          <Buttons getData={this.getData}/>
        {/* {
          btns.map(
            (item)=>(
              <button 
                key={item}
                onClick={()=>this.getData(item)}
              >
                {item}
              </button>
          ))
        } */}
        <List data={data}/>
      </div>
    }
  }

//命名导出
let a=200;
export {Home}
export{a}
//默认导出（当前文件只能导出一个）
